.content{
    /* min-height: 80vh; */
    margin-bottom: 2rem;
}
/* // navbar  */
.bg-color{
    /* background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(109,127,253,1) 0%, rgba(168,91,245,1) 90% ) !important; */
 background: linear-gradient(90deg, #7a2db7 0%, #991414 100%) !important;
     /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
/* /// registration/login page styling start */
.register-page{
    width: 100%;
    height: 100vh;
    background: url("https://images.pexels.com/photos/6289064/pexels-photo-6289064.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
    background-position: center;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
}
.register-page form{

    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(18 0 48);
    padding: 2rem;
    border-radius: 10px;
}
/*------------  registration/login  end--------------*/

/* -----------footer styling start---------------*/
.footer {
    position: sticky;
    bottom: 0;
    width: 100%;
    background: linear-gradient(90deg, #7a2db7 0%, #991414 100%) !important;
}
/* //footer styling end  */



/* -------- transaction page styling start----------- */
.filter{
    display: flex;
    justify-content: space-between;
    padding: 1rem 1rem;
    box-shadow: 0 5px 2px rgba(91, 0, 196, 0.13);
    align-items: center;
    background: white;
    margin: 1rem 0;
    background: linear-gradient(90deg, #7a2db7 0%, #991414 100%) !important;
   
}

.filter .charticons{
    border-radius: 10px;
    box-shadow: 0 5px 6px rgba(0, 0, 0, 0.281);
    padding: 0.5rem ;
    border: 1px solid rgb(197, 197, 197);
  
}
.filter .charticons:hover{
    border: 1px solid rgb(22, 136, 230);
}
.filter .charticons svg{
    font-size: 1.6rem;
    cursor: pointer;
}
.active-icon{
    color: var(--primary);
    
}
.inactive-icon{
    color: rgb(134, 134, 134);

}
.row{
    gap: 1rem;
}
.ant-table thead th {
    background: #991414 !important;
    color: white !important;
  
}
.ant-table tbody  {
    background: linear-gradient(90deg, #7a2db7 0%, #991414 100%) !important;
    color: rgb(255, 255, 255) !important;
  
}
.ant-table tbody tr:hover  {
    color: rgb(0,0, 0) !important;
  
}
/*-------------- transaction page styling  end------------*/
.action-container{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.action-icon{
    font-size: 1.5rem;
    cursor: pointer;
}


/* /responsive  */
@media screen and (max-width:760px) {
    .ant-table-body td {
        text-align: center !important;
    }
    .action-container{
        flex-direction: column;
          justify-content: center;
          align-items: center;
          gap: 1rem;
      }
  
}
@media screen and (max-width:620px) {
 
    .filter{
        flex-direction: column;
        align-items: flex-start;
        gap: 2rem;
    }
 
}